@import url(https://fonts.googleapis.com/css?family=Merriweather|Montserrat:400,700" rel="stylesheet);
@import url(https://fonts.googleapis.com/css?family=Merriweather|Montserrat:400,700" rel="stylesheet);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html {
  text-align: center;
  font-family: 'Montserrat', sans-serif;
  background: -webkit-gradient(linear, left top, right bottom, from(black), to(rgb(66, 3, 66))) no-repeat center center fixed;
  background: linear-gradient(to bottom right, black, rgb(66, 3, 66)) no-repeat center center fixed;
  /* background: url(backgroundpic.jpeg) no-repeat center center fixed;  */
  background-size: cover;
  padding-right: auto;
}


.navLinks {
  list-style: none;
  padding-left: 0;
  height: 50px;
  font: bold 30px/40px 'Montserrat', sans-serif;
  text-transform: uppercase;
  align-content: center;
}

.navLinks li {
  display: inline-block;
  padding: 0 30px;
}

.navLinks a {
  color:  white;
  text-decoration: none;
  font-size: 25px;
  -webkit-transition: color 0.25s;
    transition:         color 0.25s;
}

.navLinks a.active {
  color: rgb(142, 140, 143);
}

/* .navLinks a.active:hover {
  color: rgb(85, 83, 83);
  
} */

.navLinks a:hover {
  color: rgb(149, 96, 202);
  text-shadow: 2px 2px 2px black;
}

.App {
  text-align: center;
  display: inline-block; 
}
.App div {
  color: white;
  font: bold 50px/60px 'Montserrat', sans-serif;
  padding-top: 100px;
  padding-bottom: 0px;
}

.App div p {
  color: white;
  font: 20px 'Merriweather', serif;
  /* position: center;
  width: 500px;
  height: 100px;
  line-height: 20px;
  text-align: center; */
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 40%;
}

.App div a {
  background:transparent;
  border: none;
  padding: 1rem 2rem;
  margin: 0;
  text-decoration: none;
}
.App div a i.fab {
  font-size: 35px;
  color: white;
  -webkit-transition: color 0.25s;
    transition:         color 0.25s;
}

.App div a i.fab:hover {
  font-size: 34px;
  color: rgb(149, 96, 202);
  text-shadow: 2px 2px 2px black;
  -webkit-transition-delay: 1ms;
          transition-delay: 1ms;
}

.Resume div p {
    text-align: center; 
    padding-top: 0px;
    color: white;
    text-transform: uppercase;
    margin-top: -10px;
    font: bold 30px/40px 'Montserrat', sans-serif;
}

.Resume div p:hover {
    color: rgb(216, 214, 218);
}
.Resume div {
    margin-top: -40px;
}
.Aboutme div{
    padding-top: 1px;
} 
.Aboutme {
    display: inline-block;
}
.Aboutme p{
    text-align: center;
    padding-top: 30px;
}

.Aboutme p i.fas {
    font-size: 20px;
    color: white;
    padding-right: 10px;
}

.container {
    padding-left: 50px; 
    display: inline-block;
} 
.image {
    border: 3px solid black;
    opacity: 1;
    display: block;
    width: 20%;
    border-radius: 50%;
}

h1 {
    font-size: 30px;
}



